import {
    Argument,
    defineFunction,
    parseArgAsString,
} from './definitions-utils';
import { Atom } from '../core/atom-class';
import { AccentAtom } from '../core-atoms/accent';
import { OverunderAtom } from '../core-atoms/overunder';
import { Style } from '../public/core';

const ACCENTS = {
    acute: '\u02ca',
    grave: '\u02cb',
    dot: '\u02d9',
    ddot: '\u00a8',
    mathring: '\u02da',
    tilde: '\u007e',
    bar: '\u02c9',
    breve: '\u02d8',
    check: '\u02c7',
    hat: '\u005e',
    vec: '\u20d7',
};

defineFunction(Object.keys(ACCENTS), '{body:auto}', {
    createAtom: (command: string, args: Argument[], style: Style): Atom =>
        new AccentAtom(command, args[0] as Atom[], {
            accentChar: ACCENTS[command.slice(1)],
            style,
        }),
});

defineFunction(['widehat', 'widecheck', 'widetilde'], '{body:auto}', {
    createAtom: (command: string, args: Argument[], style: Style): Atom => {
        // Pick the correct SVG template based on the length of the body
        const baseString = parseArgAsString(args[0] as Atom[]);
        return new AccentAtom(command, args[0] as Atom[], {
            style,
            svgAccent:
                command.slice(1) +
                (baseString.length > 5
                    ? '4'
                    : ['1', '1', '2', '2', '3', '3'][baseString.length]),
        });
    },
});

defineFunction('utilde', '{body:auto}', {
    createAtom: (command: string, args: Argument[], style: Style): Atom => {
        const baseString = parseArgAsString(args[0] as Atom[]);
        const accent =
            'widetilde' +
            (baseString.length > 5
                ? '4'
                : ['1', '1', '2', '2', '3', '3'][baseString.length]);
        return new OverunderAtom(command, {
            body: args[0] as Atom[],
            svgBelow: accent,
            style,
        });
    },
});

/*
 * From plain.tex
 *
 */

defineFunction('^', '{:string}', {
    createAtom: (command: string, args: Argument[], style: Style): Atom =>
        new Atom('mord', {
            command,
            isExtensibleSymbol: false,
            isFunction: false,
            limits: 'nolimits',
            style: style,
            value: args[0]
                ? {
                      a: '',
                      e: '',
                      i: '',
                      o: '',
                      u: '',
                      A: '',
                      E: '',
                      I: '',
                      O: '',
                      U: '',
                  }[args[0] as string] ?? '^'
                : '^',
        }),
});

defineFunction('`', '{:string}', {
    createAtom: (command: string, args: Argument[], style: Style): Atom =>
        new Atom('mord', {
            command,
            isExtensibleSymbol: false,
            isFunction: false,
            limits: 'nolimits',
            style: style,
            value: args[0]
                ? {
                      a: '',
                      e: '',
                      i: '',
                      o: '',
                      u: '',
                      A: '',
                      E: '',
                      I: '',
                      O: '',
                      U: '',
                  }[args[0] as string] ?? '`'
                : '`',
        }),
});

defineFunction("'", '{:string}', {
    createAtom: (command: string, args: Argument[], style: Style): Atom =>
        new Atom('mord', {
            command,
            isExtensibleSymbol: false,
            isFunction: false,
            limits: 'nolimits',
            style: style,
            value: args[0]
                ? {
                      a: '',
                      e: '',
                      i: '',
                      o: '',
                      u: '',
                      A: '',
                      E: '',
                      I: '',
                      O: '',
                      U: '',
                  }[args[0] as string] ?? '\u005e'
                : '\u005e',
        }),
});

defineFunction('~', '{:string}', {
    createAtom: (command: string, args: Argument[], style: Style): Atom =>
        new Atom('mord', {
            command,
            isExtensibleSymbol: false,
            isFunction: false,
            limits: 'nolimits',
            style: style,
            value: args[0]
                ? { n: '', N: '', a: '', o: '', A: '', O: '' }[
                      args[0] as string
                  ] ?? '\u00B4'
                : '\u00B4',
        }),
});

defineFunction('c', '{:string}', {
    createAtom: (command: string, args: Argument[], style: Style): Atom =>
        new Atom('mord', {
            command,
            isExtensibleSymbol: false,
            isFunction: false,
            limits: 'nolimits',
            style: style,
            value: args[0] ? { c: '', C: '' }[args[0] as string] ?? '' : '',
        }),
});
